.text30{
  font-size: 1.875em;
  line-height: 142%;
}

.light-text{
  font-weight: 300;
}

.text-block{
  font-weight: 300;
  p{
    line-height: inherit;
    @include lastM(0 0 .25em)
  }
  span,
  a{
    color: $secondary;
    font-weight: 700;
  }
  a{
    text-decoration-line: underline;
  }
}

@media screen and (max-width: 992px){
  .text30{
    font-size: .875em;
  }
}
