.main{
  padding: 1.7em 0 13.2em;
  position: relative;
  &__inner{
    width: 37.6875em;
    max-width: 100%;
    position: relative;
  }
  &__logo{
    width: 100%;
    display: block;
  }
  &__text{
    margin-top: .53em;
    max-width: 34.8125em;
  }
  &__img{
    position: absolute;
    left: 100%;
    top: 2.25em;
    margin-left: 1.25em;
    width: 43.0625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img2{
    position: absolute;
    left: 0;
    bottom: 16.63em;
    width: 20.125em;
  }
  &__img3{
    position: absolute;
    right: 0;
    top: 6.25em;
    width: 10.75em;
  }
}


.info{
  padding: .8em 0;
  background: #FDEEC2;
  position: relative;
  @media screen and (min-width: 993px){
    .container{
      padding: 0 11.8em;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: 37em calc(100% - 41.44em);
    gap: 4.44em;
  }
  &__text{
    padding-top: 3.2em;
    max-width: 39.3125em;
    @media screen and (min-width: 993px){
      p{
        @include lastM(0 0 .5em)
      }
    }
  }
  &__img{
    position: absolute;
    right: 0;
    top: 3.25em;
    width: 25.75em;
  }
}

.info-slider{
  overflow: hidden;
}

.collected{
  padding: 5.94em 0 4.65em;
  &__title{
    text-align: center;
    color: #3E475B;
    font-size: 4.125em;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    z-index: 2;
  }
  &__price{
    margin-top: .3125em;
    display: flex;
    justify-content: center;
    font-family: $font2;
    color: $secondary;
    &-wrap{
      position: relative;
      span{
        font-size: 10em;
      }
      svg{
        width: 5.125em;
        height: auto;
        margin-left: .5em;
      }
      span,
      svg{
        position: relative;
        z-index: 2;
      }
      img{
        @include centerAbsolute;
        width: 43.6875em;
      }
    }
  }
}

.collected-info{
  margin-top: 3.5em;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.10);
  background-color: #fff;
  padding: 1.94em;
  &.slideToggle-block--active{
    ._hide{
      opacity: 0;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    color: #3E475B;
    font-size: 1.5em;
    font-weight: 700;
  }
  &__btn{
    margin-left: 6em;
    flex-shrink: 0;
    border: 2px solid $secondary;
    width: 4em;
    height: 4em;
    border-radius: 50%;
    @include center;
    svg{
      width: 1.5em;
      height: auto;
    }
    &:hover{
      background-color: $secondary;
      rect{
        fill: #fff;
      }
    }
  }
  .slideToggle-block__wrap{
    padding-top: 1.5em;
  }
}

.about{
  padding: 4.65em 0 6.44em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__logo{
    display: block;
    width: 16.875em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 28em) 24.5em;
    gap: 3.5em;
    margin-top: .81em;
  }
  &__text{
    padding-top: 1em;
  }
  &__nav{
    display: flex;
    margin-top: .3125em;
  }
  &__link{
    color: $secondary;
    text-decoration: underline;
  }
  &__img{
    width: 100%;
    display: block;
  }
  @media screen and (min-width: 993px){
    .text30{
      font-size: 1.5em;
    }
  }
  &__bg{
    position: absolute;
  }
  &__bg1{
    width: 17.0625em;
    top: .31em;
    left: 0;
  }
  &__bg2{
    bottom: 1.67em;
    left: 2.12em;
    width: 19.5625em;
  }
  &__bg3{
    bottom: 2.79em;
    right: 0;
    width: 14.40144em;
  }
  &__bg4{
    top: .85em;
    right: 0;
    width: 23.45469em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0 0 4.95em;
    position: relative;
    z-index: 3;
    &__inner{
      margin: 0 auto;
    }
    &__logo{
      width: 14.58944em;
      margin: -2.565em auto 0;
    }
    &__img{
      position: static;
      width: 100%;
      padding: 0 1.56em;
      margin: 1em 0 0;
    }
    &__text{
      margin-top: 1.54em;
      p{
        @include lastM(0 0 .75em)
      }
    }
    &__img2{
      bottom: -2.75em;
      width: 7.875em;
    }
    &__img3{
      top: unset;
      bottom: -2.75em;
      width: 4.3125em;
    }
  }

  .info{
    padding: 1.69em 0 1.25em;
    .container{
      position: relative;
      z-index: 2;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__col{
      &:nth-child(1){
        order: 2;
      }
    }
    &__text{
      padding: 0;
      max-width: 100%;
    }
    &__img{
      width: 7.625em;
      top: 14.75em;
    }
  }

  .info-slider{
    width: 25em;
    margin: 0 auto;
  }

  .collected{
    padding: 2.13em 0 3.78em;
    &__title{
      font-size: 1.625em;
    }
    &__price{
      margin-top: .13em;
      &-wrap{
        span{
          font-size: 4.9375em;
        }
        svg{
          width: 2.625em;
          margin-left: .19em;
        }
        img{
          width: 16.6875em;
        }
      }
    }
  }

  .collected-info{
    margin-top: 2em;
    padding: .9375em;
    &__title{
      font-size: .875em;
    }
    &__btn{
      width: 2.8125em;
      height: 2.8125em;
      svg{
        width: 1.0625em;
      }
    }
  }

  .about{
    padding: 3.78em 0 1em;
    &__logo{
      width: 6.5em;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 2.12em;
      margin-top: .3125em;
    }
    &__img{
      width: 17em;
      margin: 0 auto;
    }
    &__nav{
      margin-top: .69em;
    }
    &__bg1{
      top: -4em;
      width: 7.71719em;
      left: 2.63em;
    }
    &__bg2{
      left: unset;
      bottom: unset;
      right: 0;
      top: 2em;
      width: 8.25em;
    }
    &__bg3{
      right: unset;
      bottom: 17em;
      width: 5.8835em;
      left: 1.13em;
    }
    &__bg4{
      top: unset;
      bottom: 16.42em;
      right: 0;
      width: 7.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .info-slider{
    width: 20.625em;
    margin: 0 0 0 -.69em;
    max-width: 100%;
  }

  .collected-info{
    padding: .75em .75em 1.75em;
    position: relative;
    text-align: center;
    &__btn{
      position: absolute;
      bottom: -1.37em;
      left: 50%;
      transform: translateX(-50%);
      margin: 0;
    }
  }

  .about{
    &__img{
      width: 14em;
    }
  }
}
